.ms-cc_p {
    pointer-events: none;
    cursor: pointer;
    position: fixed;
    top: 0;
    left: 0;
    width: 45px;
    height: 45px;
    z-index: 5;
    border-radius: 50%;
}
.ms-cc_p .cursor-view {
    position: relative;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    width: 45px;
    height: 45px;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    grid-auto-columns: 1fr;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    border: 0 #fff;
    border-radius: 100vw;
    color: #fff;
    line-height: 1;
    font-size: 18px;
    text-transform: uppercase;
    opacity: 0;
    -webkit-backdrop-filter: blur(10px) brightness(100%);
    backdrop-filter: blur(10px) brightness(100%);
    -webkit-transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1);
    transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1);
}
.ms-cc_p .cursor-view::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-contrast-higher);
    opacity: 0.4;
}
.ms-cc_p .cursor-view .cursor-text {
    position: relative;
    -webkit-transform: translate(101%, 0);
    -ms-transform: translate(101%, 0);
    transform: translate(101%, 0);
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-transition-delay: 0.1s;
    transition-delay: 0.1s;
    -webkit-transition: -webkit-transform 0.7s cubic-bezier(0.23, 1, 0.32, 1);
    transition: -webkit-transform 0.7s cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 0.7s cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 0.7s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 0.7s cubic-bezier(0.23, 1, 0.32, 1);
}
.ms-cc_p .cursor-text-holder {
    position: absolute;
    left: 2.5em;
    overflow: hidden;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
.ms-cc_p .cursor-dot {
    position: absolute;
    left: 0.6vw;
    width: 14px;
    height: 14px;
    border-radius: 100%;
    background-color: var(--color-white);
}
.ms-cc_p.active .cursor-view {
    opacity: 1;
    -webkit-transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1);
    transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1);
}
.ms-cc_p.active .cursor-view .cursor-text {
    position: relative;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
    -webkit-transition-delay: 0.15s;
    transition-delay: 0.15s;
}
