        gap: 1.6rem;
    }
    .ms-p-content__inner.top {
        flex-direction: column-reverse;
        .ms-p-cat {
            margin-bottom: 10px;
        }
    }
    .ms-p-content__inner.bottom {
        flex-direction: column;
        .ms-p-cat {
            margin-top: 10px;
        }
    }

}

.overlay .item--inner:hover,
.below .item--inner:hover {

    .ms-p-img::after {
        opacity: 1;
        top: 0rem;
    }
}

// Default
.grid-item-p:not(.fadein) .ms-p-content,
.ms-p--m .ms-p-content {
    display: flex;
    flex-direction: column;
    h3 {
    position: relative;
    display: inline-block;
    }
}
.ms-p--g2 .grid-item-p,
.ms-p--d .grid-item-p,
.ms-p--m .grid-item-p {
    &:hover h3::after {
            -webkit-transform-origin: left center;
            -ms-transform-origin: left center;
            transform-origin: left center;
            -webkit-transform: scale(1,1);
            -ms-transform: scale(1,1);
            transform: scale(1,1);
        }
}

// Hover Arrow Animation
.ms-p-arrow {
    position: absolute;
    transform: rotate(45deg) scale(0);
    z-index: 1;
    right: 15px;
    top: 15px;
    background-color: var(--color-bg);
    width: 45px;
    height: 45px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    will-change: transform;
    transition: transform .4s cubic-bezier(.4,0,0,1);

    svg {
        width: 28px;
        fill: var(--color-contrast-higher);
    }

}
.grid-item-p .item--inner:hover {
    .ms-p-arrow {
        transform: rotate(45deg) scale(1);
    }
    .ms-p-arrow svg {
        animation: MoveScaleUpInitial .3s forwards, MoveScaleUpEnd .3s .2s forwards;
    }
}


