// Single Portfolio Page

// Navigation
.ms-spn--wrap {
    margin: 3rem auto 8vh;

    .ms-spn--text {
        text-transform: uppercase;
        font-weight: bold;
        margin: 3rem 0;
        
        h3 {
            text-transform: uppercase;
            color: var(--color-contrast-medium);
            font-size: 25px;
        }
    }
    .ms-spn--content {
        height: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        align-items: center;
        justify-content: center; 

        .col-md-4 {
            text-align: center;
        }
        
        .ms-spn--link {
            position: relative;
            z-index: 2;
            font-size: 0.8vw;
            text-transform: none;

            h1 {
                text-transform: uppercase;
                color: var(--color-contrast-higher);
                font-weight: 900;
                transition: color .3s;
            }
            .ms-spn--head {
                display: flex;
                align-items: center;
                margin-bottom: 8pt;
                justify-content: center;

                h3 {
                    font-size: 48px;
                }

            }

        }

        .ms-spn--thumb {
            height: 100%;
            aspect-ratio: auto 1 / 1;
            margin: auto;
            border-radius: 24pt;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            will-change: transform;
            transition: all 1s cubic-bezier(.575,.015,0,.995);

            img {
                object-fit: cover;
                object-position: center;
                width: 100%;
                height: 100%;
                will-change: transform;
                transition: margin-top 1s cubic-bezier(.575,.015,0,.995), transform 1s cubic-bezier(.575,.015,0,.995);
            }

        }

        .ms-spn--title {
            display: inline-flex;
            margin: 20px 0;
            
            h1 {
                font-size: 24px;
            }
        }

        a h5 {
            &::after {
                content: '';
                display: block;
                position: absolute;
                background-color: var(--color-primary);
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                z-index: -1;
                -webkit-transition: height .3s cubic-bezier(.23,1,.32,1);
                transition: height .3s cubic-bezier(.23,1,.32,1);
            }
        }

        a:hover h5::after {
            height: 4px;
        }

    }

}
@media (max-width: 1921px) {
    
    .ms-spn--wrap .ms-spn--content .ms-spn--link {
